.footer{
    margin:50px 170px;
    display: flex;
    flex-direction: column;
    gap: 30px;

}
.footer-top{
    display: flex;
    justify-content: space-between;
}
.footer-top-left p{
    font-size: 20px;
    max-width: 400px;

}
.footer-top-right{
    display: flex;
    align-items: center;
    gap:35px

}
.footer-email-input{
    display: flex;
    gap: 30px;
    padding: 20px 30px;
    border-radius: 50px;
    background: #32323b;
    padding-right: 80px;
}
.footer-email-input input{
    outline:none;
    border:none;
    background:transparent;
    color: aliceblue;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 18px;

}
.footer-subscribe{
    font-size: 20px;
    padding: 22px 50px;
    border-radius: 50px;
    background:linear-gradient(270deg, #DF8908 10%, #B415fF 100%);
    cursor: pointer;
    transition: 0.4s;
}
.footer-bottom{
    display: flex;
    justify-content: space-between;
    font-size: 18px;

}
.footer-bottom-right{
    display: flex;
    gap: 50px;
    margin-bottom: 50px;

}
.footer-email-input:hover{
    border: 2px snow;

}
.footer-subscribe:hover{
    transform: scale(1.15);
}
@media(max-width:768px){
    .footer{
        margin:50px 70px;
    }
    .footer-top{
        flex-direction: column;
        gap: 50px;
    }
    .footer-bottom{
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 50px;
    }
}