.hero{
    display: flex;
    align-items: center;
    flex-direction: column;
    user-select: none;
    gap :35px;
}
.hero img{
    margin-top: 125px;
    width: 250px;
    height:250px;
    border-radius:50%

}
.hero h1{
    text-align: center;
    width :60%;
    font-size: 70px;
    font-weight: 60;
}
.hero h1 span{
    background: linear-gradient( 270deg, #DF8908 10%, #B415fF 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.hero p{
    width: 50%;
    font-size: 24px;
    text-align: center;
    line-height: 40px;


}
.hero-action{
    display: flex;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight:500;
    margin-bottom: 50px;

}
.hero-connect{
    padding: 25px 45px;
    border-radius: 50px;
    background: linear-gradient(264deg, #DF8908 -5.09%,#8415FF 106.28%);
    cursor: pointer;
}
.hero-resume{
    padding: 25px 75px;
    border-radius: 50px;
    border: 2px solid white;
    cursor: pointer;
}
.hero-connect:hover{
    border: 2px solid whitesmoke;

}.hero-resume:hover{
    border-color: #8415FF;
}
@media(max-width:768px){
    .hero img{
        margin-top: 120px;
        width: 260px;

    }
    .hero h1{
        margin-top: 50px;
        width: 90%;
        font-size: 50px;

    }
    .hero p{
        width: 60%;
        font-size: 18px;
        line-height: 32px;
        margin: 10px 0px;
    }
    .hero-action{
        gap: 16px;
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 50px;
    }
    .hero-connect{
        padding: 20px 30px;
        border-radius: 40px;

    }
    .hero-resume{
        padding: 20px 50px;
        border-radius: 40px;
    }
}